.error-indicator {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #c70d3a;
  height: 100%;
  justify-content: center;
}

.error-indicator img {
  width:7%;
  height: auto;
  margin-bottom: 1rem;
}

.error-indicator .boom {
  font-size: 1.3rem;
  font-weight: bold;
}