.item-list .list-group-item {
    cursor: pointer;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.item-list {
    border-radius: 5px;
    width: 49%;
    height:20rem;
    overflow-y: auto;
    background-color: #303030;
}

.item-list .list-group-item:hover {
    background-color: #444;
}